import { Injectable } from '@angular/core';
import { Http, Response, RequestOptions, Headers } from '@angular/http';
// import { Observable } from 'rxjs/Observable';
import 'rxjs/Rx';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';
// import { Observable } from 'rxjs/Rx';
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/catch';
import {ApiManhClient} from './ApiManhClient';

@Injectable()
export class PutService {
  readonly client = new ApiManhClient({accessToken: "Dummytoken"})
  headers: Headers;
  options: RequestOptions;
  aiSessionID : "";

  constructor(private http: Http) {
      this.headers = new Headers({ 'Content-Type': 'application/json' });
      this.options = new RequestOptions({ headers: this.headers });
      this.aiSessionID = "";
  }

  setAiSessionId(aiSessionID){
    this.aiSessionID = aiSessionID;
  }
  putData(url: string, param: any){
      //let body = {};
//     //   body.query = param;
//     //   body.aiSessionID = this.aiSessionID;

    return this.client.textRequest(param);
  /*return this.http
      .post(url, body, this.options)
      .map(this.extractData)
      .catch(this.handleError);*/
  }   
  // JSON.stringify(param)
// PutData(url:string,param:any){

// }


  private extractData(res: any) {
      let resObj = JSON.parse(res.text());
      return resObj.text || {};
  }

  private handleError(error: any) {
      let errMsg = (error.message) ? error.message :
          error.status ? `${error.status} - ${error.statusText}` : 'Server error';
      // console.error(errMsg);
      return Observable.throw(errMsg);
  }
}


