import { ApiAiConstants } from "api-ai-javascript/ts/ApiAiConstants";
import { ApiAiClientConfigurationError } from "api-ai-javascript/ts/Errors";
import { EventRequest } from "api-ai-javascript/ts/Request/EventRequest";
import TextRequest from "api-ai-javascript/ts/Request/TextRequest";
import {ManhApiConstants} from "../models/ManhApiConstants";
// import { TTSRequest } from "./Request/TTSRequest";

import { IApiClientOptions, IRequestOptions, IServerResponse, IStringMap } from "api-ai-javascript/ts/Interfaces";
import { ApiAiClient } from "api-ai-javascript/ts/ApiAiClient";

export * from "api-ai-javascript/ts/Interfaces";
export {ApiAiConstants} from "api-ai-javascript/ts/ApiAiConstants";

export class ApiManhClient extends ApiAiClient{

    
    private apiManhBaseUrl: string;
    

    constructor(options: IApiClientOptions) {
        super(options);
        this.apiManhBaseUrl = options.baseUrl || ManhApiConstants.DEFAULT_BASE_URL;
        
    }

    public textRequest(query, options: IRequestOptions = {}): Promise<IServerResponse> {
        if (!query) {
            throw new ApiAiClientConfigurationError("Query should not be empty");
        }
        options.query = query;
        return new TextRequest(this, options).perform();
    }

    public eventRequest(eventName, eventData: IStringMap = {},
                        options: IRequestOptions = {}): Promise<IServerResponse> {
        if (!eventName) {
            throw new ApiAiClientConfigurationError("Event name can not be empty");
        }
        options.event = {name: eventName, data: eventData};
        return new EventRequest(this, options).perform();
    }

    // @todo: implement local tts request
    /*public ttsRequest(query) {
        if (!query) {
            throw new ApiAiClientConfigurationError("Query should not be empty");
        }
        return new TTSRequest(this).makeTTSRequest(query);
    }*/

    /*public userEntitiesRequest(options: IRequestOptions = {}): UserEntitiesRequest {
        return new UserEntitiesRequest(this, options);
    }*/

    public getApiBaseUrl(): string {
        return (this.apiManhBaseUrl) ? this.apiManhBaseUrl : ManhApiConstants.DEFAULT_BASE_URL;
    }

    
}



