import { Component, OnInit, Input } from '@angular/core';
import { ChatService } from '../../services/chat.service';
import { AuthService } from '../../services/auth.service';
import { ChatMessage } from '../../models/chat-message.models';
import { PutService } from '../../services/put.service';

@Component({
  selector: 'app-message',
  templateUrl: './message.component.html',
  styleUrls: ['./message.component.css']
})
export class MessageComponent implements OnInit {

  responseMessage:String;
  userMessage: String;
  url: string;
  
  @Input() chatMessage: string;
  // userEmail: string;
  // userName: string;
  messageContent: string;
  timeStamp: Date = new Date();
  // isOwnMessage: boolean;
  // ownEmail: string;

  constructor(private authService: AuthService, private putService: PutService) {
 

    this.url = 'http://blrlw6487:8080/virtualAssistant/getMessageFromVA';
  }


  ngOnInit() {
    this.messageContent = this.chatMessage;
    console.log(this.messageContent);

    this.edit(this.messageContent);
  }
   
  ngOnChanges() {
    this.messageContent = this.chatMessage;
    // this.ngOnInit();
  }

  edit(arg: any) {
    console.log("edit-> " + arg);
    this.putService.putData(this.url, arg).
        then(
        res => {
          this.responseMessage = res.result.fulfillment.speech;
          console.log(this.responseMessage);

        },
        error => {
            if (error == "400 - Bad Request") {
                this.responseMessage=error;
                this.errorOut();
            }
        }
        );
}

errorOut() {
  this.ngOnInit();
}
  

}



